import React from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Paper,
  Slider,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  FormHelperText,
} from '@mui/material';
import { useSettings, Settings } from '../context/SettingsContext';

const modeDescriptions = {
  normal: 'Звичайний режим: поступове збільшення швидкості читання з можливістю паузи. Найкраще підходить для початківців та щоденних тренувань.',
  sprint: 'Режим спринту: короткі інтенсивні сесії з високою швидкістю читання. Допомагає подолати бар\'єр швидкості та розвинути периферійний зір.',
  marathon: 'Режим марафону: тривале читання з помірною швидкістю для розвитку витривалості. Фокус на збереженні концентрації протягом довгого часу.'
};

const SettingsPage = () => {
  const navigate = useNavigate();
  const { settings, updateSettings } = useSettings();

  const handleStart = () => {
    navigate('/trainer');
  };

  const handleSettingsChange = (key: keyof typeof settings) => (
    event: React.ChangeEvent<HTMLInputElement> | any
  ) => {
    const value = event.target.value;
    updateSettings({ [key]: value });
  };

  return (
    <Container maxWidth="sm">
      <Box sx={{ mt: 4, mb: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom align="center">
          Налаштування
        </Typography>
        <Paper elevation={3} sx={{ p: 3, mt: 3 }}>
          <Box sx={{ mb: 3 }}>
            <Typography gutterBottom>
              Швидкість (слів за хвилину): {settings.wordsPerMinute}
            </Typography>
            <Slider
              value={settings.wordsPerMinute}
              onChange={handleSettingsChange('wordsPerMinute')}
              min={100}
              max={1000}
              step={50}
            />
          </Box>

          <Box sx={{ mb: 3 }}>
            <Typography gutterBottom>
              Розмір шрифту: {settings.fontSize}px
            </Typography>
            <Slider
              value={settings.fontSize}
              onChange={handleSettingsChange('fontSize')}
              min={16}
              max={48}
              step={2}
            />
          </Box>

          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel>Кількість слів за раз</InputLabel>
            <Select
              value={settings.wordsAtOnce}
              onChange={(e) => updateSettings({ wordsAtOnce: e.target.value as Settings['wordsAtOnce'] })}
              label="Кількість слів за раз"
            >
              <MenuItem value={1}>1 слово</MenuItem>
              <MenuItem value={2}>2 слова</MenuItem>
              <MenuItem value={3}>3 слова</MenuItem>
              <MenuItem value={4}>4 слова</MenuItem>
              <MenuItem value={5}>5 слів</MenuItem>
              <MenuItem value={6}>6 слів</MenuItem>
              <MenuItem value={7}>7 слів</MenuItem>
            </Select>
            <FormHelperText>
              Оберіть кількість слів, які ви хочете бачити одночасно
            </FormHelperText>
          </FormControl>

          <FormControl fullWidth sx={{ mb: 3 }}>
            <InputLabel>Режим читання</InputLabel>
            <Select
              value={settings.mode}
              onChange={handleSettingsChange('mode')}
              label="Режим читання"
            >
              <MenuItem value="normal">Звичайний режим</MenuItem>
              <MenuItem value="sprint">Режим спринту</MenuItem>
              <MenuItem value="marathon">Режим марафону</MenuItem>
            </Select>
            <FormHelperText sx={{ 
              mt: 1, 
              backgroundColor: 'rgba(0, 0, 0, 0.03)', 
              p: 1, 
              borderRadius: 1,
              minHeight: '60px'
            }}>
              {modeDescriptions[settings.mode as keyof typeof modeDescriptions]}
            </FormHelperText>
          </FormControl>

          <Button
            variant="contained"
            color="primary"
            fullWidth
            size="large"
            onClick={handleStart}
          >
            Почати читання
          </Button>
        </Paper>
      </Box>
    </Container>
  );
};

export default SettingsPage;
